import { fetchData } from "@/utils/unils";
import React, { useEffect, useState } from "react";

type PriorityItem = {
	status: string;
	count: number;
};

export type Assignee = {
	id: string;
	name: string;
	avatar: string;
};

export type Task = {
	id: string;
	title: string;
	description: string;
	status: "to-do" | "in-progress" | "review" | "completed";
	createdAt: string;
	updatedAt: string;
	dueDate: string;
	assignees?: Assignee[];
};

export type Tasks = Task[];

export type GroupedTasks = {
	priority: Task["status"];
	items: Task[];
};

export type GroupedTasksArray = GroupedTasks[];
export default function ListOfTask() {
	const [elements, setElements] = useState<Tasks>([]);
	const [priority, setPriority] = useState<PriorityItem[]>([
		{ status: "to-do", count: 0 },
		{ status: "in-progress", count: 0 },
		{ status: "review", count: 0 },
		{ status: "completed", count: 0 },
	]);

	useEffect(() => {
		(async () => {
			const data = await fetchData();
			console.log(data);
			setPriority((prev) => {
				return prev.map((p) => {
					const count = data.filter(
						(task: Task) => task.status === p.status
					).length;
					return { ...p, count };
				});
			});
			setElements(data);
		})();
	}, []);
	return (
		<div className="flex justify-between">
			{priority.map((el, key) => {
				const tasksForStatus = elements.filter(
					(task) => task.status === el.status
				);
				return (
					<div key={key}>
						{el.status} ({el.count})
						{tasksForStatus.map((task) => {
							return <CardOfTask />;
						})}
					</div>
				);
			})}
		</div>
	);
}
